.container {
  justify-content: center;
  gap: 30px;
  display: flex;
}

button {
  cursor: pointer;
  color: #fff;
  background: #000a47;
  border: 0;
  border-radius: 3px;
  outline: none;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.15;
  display: inline-block;
}

button:disabled {
  cursor: pointer;
  color: #3a4149;
  background: #e7ebee;
  border: 0;
  border-radius: 3px;
  outline: none;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.15;
  display: inline-block;
}

